import React, { useId } from 'react';

type LabelledTextFieldProps = {
  label: string,
  children: (id: string) => React.ReactNode,
}

export const LabelledTextField = ({ label, children }: LabelledTextFieldProps) => {
  const id = useId();
  return (
    <div className="w-full">
      <label htmlFor={id} className="flex flex-col">
        <span className="font-uni-sans text-base font-black uppercase">{label}</span>
        {children(id)}
      </label>
    </div>
  );
};
