import React, { SVGProps, useId } from 'react';
import { ThemeSvgGradient } from '../theme-svg-gradient/theme-svg-gradient';

const BorderedFrame = (props: SVGProps<SVGSVGElement>) => {
  const id = useId();
  return (
    <svg
      fill="none"
      viewBox="0 0 526 587"
      xmlns="http://www.w3.org/2000/svg"
      preserveAspectRatio="none"
      stroke={`url(#${id})`}
    // eslint-disable-next-line react/jsx-props-no-spreading
      {...props}
    >
      <rect x={0.5} y={0.5} width={525} height={586} className="rx-6" />
      <ThemeSvgGradient id={id} />
    </svg>
  );
};

type BorderedCardProps = React.HTMLAttributes<HTMLDivElement> & {
  children: React.ReactNode,
  className?: string,
}

export const BorderedCard = ({ children, className }: BorderedCardProps) => (
  <div className={`${className} relative z-20 flex h-fit w-full max-w-[500px] flex-col gap-12 p-12`}>
    <BorderedFrame className="absolute top-0 left-0 -z-10 h-full w-full fill-main-purple/70" />
    {children}
  </div>
);
