/* eslint-disable react/jsx-props-no-spreading */
import { PrimaryButton } from 'components/molecues/button';
import { Checkbox } from 'components/molecues/checkbox/checkbox';
import { TextField, TextArea } from 'components/molecues/text-field/text-field';
import { ContactDTO } from 'domain/contact/contact-dto';
import { sendMail } from 'infrastructure/contact/send-mail';
import React from 'react';
import { useForm } from 'react-hook-form';

export const ContactForm = () => {
  const {
    register, handleSubmit, getValues, reset,
  } = useForm<ContactDTO>();

  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    handleSubmit(async () => {
      const responseCode = await sendMail(getValues());
      if (responseCode === 200) reset();
    })(e);
  };

  return (
    <form onSubmit={onSubmit} className="flex h-fit w-full flex-col items-center justify-center gap-4">
      <TextField {...register('email', { required: true })} label="e-mail" type="email" />
      <TextField {...register('phone', { required: true })} label="numer kontaktowy" type="tel" />
      <TextArea {...register('message', { required: true })} label="treść" placeholder="Opisz dokładnie czego potrzebujesz" className="h-40" required />
      <div className="flex w-full flex-col gap-4 md:flex-row">
        <PrimaryButton type="submit" className="order-2 w-full md:order-1">Wyślij</PrimaryButton>
        <div className="order-1 md:order-2">
          <Checkbox {...register('consent', { required: true })} required>Oświadczam, że zapoznałem się z Polityką Prywatności...</Checkbox>
        </div>
      </div>
    </form>
  );
};
