/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { forwardRef, useId } from 'react';
import * as RadixCheckbox from '@radix-ui/react-checkbox';
import { Icon } from 'components/atoms/icon/icon';

type CheckboxProps = RadixCheckbox.CheckboxProps & React.RefAttributes<HTMLButtonElement> & {
  children: React.ReactNode,
}

export const Checkbox = forwardRef<
  HTMLButtonElement, CheckboxProps
>(({ className, children, ...restProps }: CheckboxProps, ref) => {
  const id = useId();
  return (
    <div className="flex w-full items-center gap-3">
      <RadixCheckbox.Root ref={ref} id={id} {...restProps} className={`${className} flex aspect-[1/1] h-4 items-center justify-center rounded border border-main-white p-1`}>
        <RadixCheckbox.Indicator>
          <Icon name="Ok" className="aspect-[1/1] h-2 fill-main-white" />
        </RadixCheckbox.Indicator>
      </RadixCheckbox.Root>
      <label htmlFor={id} className="text-xs">
        {children}
      </label>
    </div>
  );
});
