/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable react/prop-types */
import React from 'react';
import { LabelledTextField } from '../labelled-text-field/labelled-text-field';

type TextFieldProps = React.DetailedHTMLProps<
  React.InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
> & {
  label: string;
};
// eslint-disable-next-line react/prop-types
export const TextField = React.forwardRef<HTMLInputElement, TextFieldProps>(
  ({ className, label, ...restProps }: TextFieldProps, ref) => (
    <LabelledTextField label={label}>
      {(id) => (
        <input
          ref={ref}
          id={id}
          className={`${className} rounded-l-2xl bg-[rgba(43,42,47,0.6)] pt-4 pl-6 pb-5 text-base`}
          {...restProps}
        />
      )}
    </LabelledTextField>
  ),
);

type TextAreaProps = React.DetailedHTMLProps<
  React.TextareaHTMLAttributes<HTMLTextAreaElement>,
  HTMLTextAreaElement
> & {
  label: string;
  resize?: boolean;
};

export const TextArea = React.forwardRef<HTMLTextAreaElement, TextAreaProps>(
  ({
    className, label, resize, ...restProps
  }: TextAreaProps, ref) => (
    <LabelledTextField label={label}>
      {(id) => (
        <textarea
          ref={ref}
          style={{ resize: resize ? 'vertical' : 'none' }}
          id={id}
          className={`${className} rounded-l-2xl bg-[rgba(43,42,47,0.6)] px-6 pt-4 pb-5 text-base`}
          {...restProps}
        />
      )}
    </LabelledTextField>
  ),
);
